.destination
{
    margin: 4rem 6rem;

}

.destination h1{
    font-size: 3rem;
   
}

.first-des
{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top:4.5rem;

}
.first-des-reverse
{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    margin-top:4.5rem;

}


.des-text
{
    width:45%;
    text-align: start;
    font-size: 1.1rem;
}

.des-text h2
{
    padding-bottom:1rem ;
}

.des-img
{
    width:50%;
    position: relative;
    display: flex;
    justify-content: space-between;
    z-index: -99;
}

.des-img img{
    width: 49%;
    height: 350px;
    object-fit: cover;
    border-radius: 6px;
    box-shadow: -1px 1px 62px -18px rgba(0,0,0,0.19);
}

.des-img img:nth-child(2){
 position: absolute;
 top: -10%;
 right: 0;  
}

@media screen and (max-width: 850px)
{
    .destination
    {
        margin: 4rem 2rem;
    }

    .first-des
    {
        display: flex;
        flex-direction: column-reverse;
        margin-top:2.5rem;
    }

    .first-des-reverse
    {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        margin-top:4.5rem;

    }

    .des-text
    {
        width: 100%;
      
    }

    .des-img
    {
        width: 100%;
        margin: 1.8rem 0;
    }

    .des-img img
    {
       
        height: 250px;
    }

}
